import Vue from 'vue'
import store from '../store'
import VueRouter from 'vue-router'
import home from '../views/home/home.vue'
import header from '../components/Header.vue'

Vue.use(VueRouter)

/**
 * @description meta
 * @param index {number} 
 * @param title {string} 
 * @param showLogo {boolean} logo
 * @param showMenuButton {boolean} 
 * @param needLogin {boolean} 
 * @param backPath {string} 
 * @param keepAlive {boolean} 
 * */
const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    name: 'home',
    meta: {
      index: 1,
      title: '',
      showLogo: true,
      showMenuButton: true
    },
    components: {
      header,
      default: home
    }
  },
  {
    path: '/login',
    name: 'login',
    meta: {
      title: '',
      keepAlive: false
    },
    components: {
      default: () => import('@/views/sign/sign_in.vue')
    }
  },
  {
    path: '/register',
    name: 'register',
    meta: {
      title: '',
      keepAlive: false
    },
    components: {
      default: () => import('@/views/sign/sign_up.vue')
    }
  },
  {
    path: '/registerSuccess',
    name: 'registerSuccess',
    meta: {
      title: '',
      keepAlive: false
    },
    components: {
      default: () => import('@/views/sign/sign_up_success.vue')
    }
  },
  {
    path: '/promotions',
    name: 'promotions',
    meta: {
      index: 1,
      title: '',
      showMenuButton: true
    },
    components: {
      header,
      default: () => import('@/views/promotions/promotions.vue')
    }
  },
  {
    path: '/games',
    name: 'games',
    meta: {
      index: 2,
      title: '',
      backPath: '/home'
    },
    components: {
      default: () => import('@/views/games/games.vue')
    }
  },
  {
    path: '/mien',
    name: 'mien',
    redirect: '/mien/sponsor',
    meta: {
      index: 1,
      title: '',
      showMenuButton: true
    },
    components: {
      header,
      default: () => import('@/views/mien/mien.vue')
    },
    children: [
      {
        path: '/mien/sponsor',
        name: 'mienSponsor',
        meta: {
          index: 1,
          title: '',
          showMenuButton: true
        },
        component: () => import('@/views/mien/sponsor.vue')
      }, {
        path: '/mien/history',
        name: 'mienHistory',
        meta: {
          index: 1,
          title: '',
          showMenuButton: true
        },
        component: () => import('@/views/mien/history.vue')
      }, {
        path: '/mien/videos',
        name: 'mienVideos',
        meta: {
          index: 1,
          title: '',
          showMenuButton: true
        },
        component: () => import('@/views/mien/videos.vue')
      }
    ]
  },
  {
    path: '/loadingGame',
    name: 'loadingGame',
    component: () => import('@/views/common/loadingGame.vue')
  },
  {
    path: '/dewFigure',
    name: 'dewFigure',
    meta: {
      index: 2,
      title: 'PC'
    },
    components: {
      header,
      default: () => import('../views/dewFigure.vue')
    }
  },
  {
    path: '/appDownload',
    name: 'appDownload',
    meta: {
      index: 1,
      title: 'APP/',
      showMenuButton: true
    },
    components: {
      header,
      default: () => import('@/views/common/iframe.vue')
    }
  },
  {
    path: '/vipCenter/vipZone',
    name: 'vipZone',
    meta: {
      index: 2,
      title: 'VIP'
    },
    components: {
      header,
      default: () => import('@/views/common/iframe.vue')
    }
  },
  {
    path: '/playerGuide',
    name: 'playerGuide',
    meta: {
      index: 2,
      title: ''
    },
    components: {
      header,
      default: () => import('@/views/common/iframe.vue')
    }
  },
  {
    path: '/faq',
    name: 'faq',
    meta: {
      index: 2,
      title: ''
    },
    components: {
      header,
      default: () => import('@/views/common/iframe.vue')
    }
  },
  {
    path: '/about',
    name: 'about',
    meta: {
      index: 2,
      title: ''
    },
    components: {
      header,
      default: () => import('@/views/common/iframe.vue')
    }
  },
  {
    path: '/nationalClub',
    name: 'nationalClub',
    meta: {
      index: 2,
      title: ''
    },
    components: {
      header,
      default: () => import('@/views/common/iframe.vue')
    }
  },
  {
    path: '/page1',
    name: 'page1',
    meta: {
      index: 2,
      title: ''
    },
    components: {
      header,
      default: () => import('../views/page1.vue')
    }
  },
  {
    path: '/page2',
    name: 'page2',
    meta: {
      title: '',
      index: 3,
      keepAlive: false
    },
    components: {
      default: () => import('../views/page2.vue')
    }
  },
  {
    path: '/page3',
    name: 'page3',
    meta: {
      title: '',
      needLogin: true,
      keepAlive: false
    },
    components: {
      default: () => import('../views/page3.vue')
    }
  },
  {
    path: '*',
    meta: {
      title: '404',
      keepAlive: false
    },
    component: () => import('@/views/common/404.vue')
  }
]

const VueRouterPush = VueRouter.prototype.push
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return {
        x: 0,
        y: 0
      }
    }
  }
})

router.push = function (to) {
  return VueRouterPush.call(this, to).catch(() => null)
}

router.beforeEach((to, from, next) => {
  if (to.meta.needLogin && !store.getters.loginStatus) {
    next({ path: '/login' })
  } else {
    next()
  }
})

export default router
